/* MOBILE */
.btn-mobile-nav {
  border: none;
  background: none;
  cursor: pointer;
  display: block;
  z-index: 1000;
}

.icon-mobile-nav {
  height: 3.2rem;
  width: 3.2rem;
  color: #333;
}

.icon-mobile-nav[name='close-outline'] {
  display: none;
}

.icon-mobile-nav[name='menu-outline'] {
  color: #fff;
}

.main-nav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  z-index: 999;

  background-color: rgb(255, 255, 255, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-in;
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.nav-open .main-nav {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.nav-open .icon-mobile-nav[name='close-outline'] {
  display: block;
}

.nav-open .icon-mobile-nav[name='menu-outline'] {
  display: none;
}

/* ************************ */
/* HERO */
/* ************************ */

.section-header {
  position: relative;
  height: 44vw;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - var(--section-rotate)), 0 100%);
  -webkit-clip-path: polygon(0 0, 100% 0, 100% calc(100% - var(--section-rotate)), 0 100%);
}

.header__hero {
  height: 100%;
}

.header__hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #673ab7;
  background: linear-gradient(to right, #512da8, #673ab7);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  opacity: 0.91;
}

.header__hero-img {
  object-fit: cover;
  height: 100%;
  width: 100%;
  object-position: 50% 25%;
}

.heading-box {
  position: absolute;
  left: 50%;
  top: 40%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.heading-primary {
  color: #eeeaf6;
  letter-spacing: 3rem;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 5.2rem;
  text-align: center;
  width: 70%;
  margin: 0 auto;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* .heading-primary span,
.heading-tertirary span {
  padding: 1rem 1.5rem;
  line-height: 1.6;
} */

.heading-primary--main {
  -webkit-animation: moveInLeft 1s ease-out;
  animation: moveInLeft 1s ease-out;
}

@-webkit-keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
    transform: translateX(-10rem);
  }
  80% {
    -webkit-transform: translateX(1rem);
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
    transform: translateX(-10rem);
  }
  80% {
    -webkit-transform: translateX(1rem);
    transform: translateX(1rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}

.intro {
  -webkit-transform: translate(15%, 0%);
          transform: translate(15%, 0%);
  margin: auto;
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  color: #eeeaf6;
  letter-spacing: 1.3rem;
  /* margin-top: 2rem; */
  display: block;
  -webkit-animation: moveInRight 1s ease-in;
  animation: moveInRight 1s ease-in;
}

@-webkit-keyframes moveInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10rem);
    transform: translateX(10rem);
  }
  80% {
    -webkit-transform: translateX(-1rem);
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}

@keyframes moveInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10rem);
    transform: translateX(10rem);
  }
  80% {
    -webkit-transform: translateX(-1rem);
    transform: translateX(-1rem);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
    transform: translate(0);
  }
}

