.form__label {
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
}

.form__label.fileInput {
  margin-bottom: 2.5rem;
  font-weight: 600;
}

.postSubmitBtn {
  margin: 1rem 11rem;
}

.login-form.new-post-form {
  max-width: 51rem;
  padding: 4.4rem 7rem;
  position: relative;
}

.file-input-box {
  display: flex;
  align-items: baseline;
  grid-gap: 2.4rem;
  gap: 2.4rem;
}

.newpost-caption-closebtn-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.btn-mobile-nav.new-post-box-close-btn {
  position: absolute;
  top: 20px;
  right: 14px;
}

.profile-card {
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0.5rem 1rem 1.5rem rgba(0, 0, 0, 0.07);
  background-color: #fff;
  transition: 0.3s all;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: flex;
  flex-direction: column;
}

.all-like-box {
  max-width: 60rem;
  height: 54rem;
  border-radius: 10px;
  margin: 10rem auto;
  box-shadow: 0.5rem 1rem 1.5rem rgba(0, 0, 0, 0.07);
  background-color: white;
  overflow-y: scroll;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  z-index: 9999;
  padding: 3rem 4rem;

  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.profile-bg-video__content {
  height: 30rem;
  width: 100%;
  object-fit: cover;
}

.profile-card__picture-img {
  object-fit: cover;
  height: 30rem;
  width: 100%;
}

.overlay-close {
  height: 3.2rem;
  width: 3.2rem;
  color: #333;
}

.all-like-box::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}

.profile-card-picture {
  position: relative;
}

.profile-card .btn {
  font-size: 1.4rem;
  padding: 1rem 1.6rem;
  font-weight: 500;
}

.profile-card__sub-heading {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.5px;
  grid-column: 1 / -1;
}

.profile-card__footer {
  padding: 0.8rem 3rem;
  font-size: 1.4rem;
  display: flex;
  margin-top: auto;
  justify-content: space-between;
}

.fas.fa-trash {
  font-size: 4rem;
  background: #e52d27; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #b31217,
    #e52d27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  -webkit-background-clip: text;
  color: transparent;
}

.all-like-box.delete-box {
  height: 26rem;
  max-width: 70rem;
  overflow-y: hidden;
}

.delete-warning {
  font-size: 2rem;
  font-weight: 700;
  display: block;
  margin-bottom: 2rem;
}

.trash-icon-box {
  border: 5px solid rgba(255, 0, 0, 0.737);
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  padding: 2.6rem;
  margin-bottom: 2rem;
}

.delete-style-box {
  text-align: center;
}

.deletePostContainer {
  width: 100%;
}

.cancel-btn {
  padding: 1rem 2rem;
  font-size: 1.6rem;
  /* text-transform: uppercase; */
  letter-spacing: 2px;
  background-color: #f1f3f5;
  color: #555;
  border-radius: 4px;
  transition: all 0.4s;
  border: none;
  cursor: pointer;
}

.cancel-btn:hover {
  background-color: #dee2e6;
}

.delete-modal-btn-box {
  display: flex;
  justify-content: space-between;
}

.cancel-btn.delete-btn {
  background-color: #ce2923;
  color: #fff;
}

.delete-post-btn {
  color: #b31217;
  background-color: white;
  border: 2px solid #b31217;
  border-radius: 8px;
  font-weight: 500 !important;
}

.delete-post-btn:active,
.delete-post-btn:hover,
.delete-post-btn:focus,
.delete-post-btn:visited {
  border: 2px solid none;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  box-shadow: none;
  background-color: #b31217 !important;
  color: #fff;
}

.edit-post-btn,
.edit-post-btn:active,
.edit-post-btn:visited,
.edit-post-btn:focus {
  background: #673ab7;
  border-radius: 8px;
}

.edit-post-btn:hover {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  background: #522e92;
}

.profile {
  padding: 5rem 0;
  flex: 1 1;
  position: relative;
}

.profile-card-container {
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 7rem;
  padding: 0 10rem;
}

.empty-post-msg {
  font-size: 2.2rem;
  text-align: center;
  font-weight: 600;
  color: #555;
}

.empty-post-msg:last-child {
  margin-bottom: 14.4rem;
}


.error-container {
  position: relative;
  padding: 5rem 6rem 8rem 6rem;
  flex: 1 1;
  margin-bottom: 30rem;
  margin-top: 10rem;
}

.error {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: 400rem;
  text-align: center;
}

.error__title {
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading-page--error {
  background-image: linear-gradient(to right, #ff7730, #eb4d4b);
  font-size: 3.5rem;
  letter-spacing: 1.6px;
  margin-bottom: 1rem;
}

.heading-error-page {
  font-size: 2.25rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(to right, #7ab9f0, #1f7dcf);
  -webkit-background-clip: text;
  color: transparent;
  letter-spacing: 0.1rem;
  line-height: 1.3;
  display: inline-block;
}

.error__emoji {
  font-size: 3.75rem;
  margin-left: 1rem;
}

.error__msg {
  font-size: 2.5rem;
  font-weight: 700;
  max-width: 50rem;
  margin: 0 auto;
}

